export default function GameResult({
    betAmount,
    ballsCount,
    rows,
    risk,
    show,
    handleClose,
    payoutMultiple,
}) {
    const showHideClassName = show ? "modal display-block" : "modal display-none";
    const [payoutSum, setPayoutSum] = useState();

    useEffect(() => {
        for (var i = 0, tmp = 0; i < payoutMultiple.length; i++) {
            tmp += payoutDefaultValue[rows - 8][risk][parseInt(payoutMultiple[i])];
        }
        setPayoutSum(tmp);
    }, [payoutMultiple, risk, rows]);

    return (
        <div className={showHideClassName}>
            <section className="modal-main">
                <section className='modal-header'>
                    <h1>Results</h1>
                </section>
                <section className='modal-content'>
                    <div className='bet-results'>
                        <div>
                            <span>Bet</span>
                            <span>
                                {Number(Number(betAmount * ballsCount).toFixed(2))}
                                <img src={avaxlogo} width={15} height={15} alt={'AvaxLogo'}></img>
                            </span>
                        </div>
                        <div>
                            <span>Multipliers</span>
                            <span>{Number(Number(Number(payoutSum).toFixed(1) / ballsCount).toFixed(2))}</span>
                        </div>
                        <div>
                            <span>Payout</span>
                            <span>
                                {Number(Number(betAmount * payoutSum).toFixed(3))}
                                <img src={avaxlogo} width={15} height={15} alt='AvaxLogo'></img>
                            </span>
                        </div>
                    </div>
                    <div className='multiplier-details'>
                    </div>
                </section>
                <section className='modal-footer'>
                    <button type="button" onClick={handleClose} className="game-close-btn">Close</button>
                </section>    
            </section>
        </div>
    );
};